export default class DataSource {
    constructor(options) {
        Object.assign(this, {
            data: [],
            cache: true,
            limit: 50,
            // offset: 0,
            total: Infinity, // 
            // order
            // filter
            promise: Promise.resolve(),
        }, options);

        //  data 
        if (options.data)
            this.total = options.data.length;
    }

    fetch(params = {}) {
        params = Object.assign({
            limit: this.limit,
            offset: this.data.length,
        }, params);
        const newOffset = params.offset + params.limit;

        // 
        if (!this.hasMore())
            return this.data.slice(0, newOffset);

        if (this.loadMore)
            return this.doLoadMore(params).then(() => this.data.slice(0, newOffset));
        else if (this.load)
            return this.doLoad(params).then(() => this.data.slice(0, newOffset));
    }

    hasMore(offset) {
        if (offset === undefined)
            offset = this.data.length;
        return offset < this.total;
    }

    clear() {
        this.data = [];
        this.total = Infinity;
    }

    /**
     * 
     * @override
     */
    doLoad(params) {
        return this.load && this.load(params).then((result) => {
            if (result instanceof Array) { //  total 
                this.total = result.length;
                this.data = result;
            } else if (result instanceof Object) { //  { total, data }
                this.total = result.total;
                this.data = result.data;
            } // 
        });
    }

    /**
     * 
     * @override
     */
    // load: undefined,

    /**
     * 
     * @override
     * @param {*} params.limit 
     * @param {*} params.offset 
     */
    doLoadMore(params) {
        return this.loadMore && this.loadMore(params).then((result) => {
            if (params.offset + params.limit <= this.data.length)
                return undefined;

            if (result instanceof Array) { //  total 
                if (!result.length) // 
                    this.total = this.data.length;
                else
                    this.data.push(...result);
            } else if (result instanceof Object) { //  { total, data }
                this.total = result.total;
                this.data.push(...result.data);
            } // 
        });
    }

    /**
     * 
     * @override
     * @param {*} params.limit 
     * @param {*} params.offset 
     */
    // loadMore: undefined,

    save() {
        // 
    }

    create() {
        // 
    }

    get() {
        // 
    }

    update() {
        // 
    }

    remove() {
        // 
    }
}
