<template src="./index.html"></template>
<script>
import Calendar from '../u-calendar.vue';
import { clickOutside } from '../../directives';
import { format, transformDate } from '../../utils/date';
import MField from '../m-field.vue';
import i18n from './i18n';
const MS_OF_DAY = 24 * 3600 * 1000;

/**
 * @class DatePicker
 * @extend Dropdown
 * @param {object}                  options                     =  
 * @param {object=null}             options.date               <=> 
 * @param {string=''}         options.placeholder         => 
 * @param {Date|string=null}        options.minDate             => 
 * @param {Date|string=null}        options.maxDate             => 
 * @param {boolean=false}           options.autofocus           => 
 * @param {boolean=false}           options.readonly            => 
 * @param {boolean=false}           options.disabled            => 
 */
export const UDatePicker = {
    name: 'u-date-picker',
    i18n,
    props: {
        date: [String, Number, Date],
        minDate: [String, Number, Date],
        maxDate: [String, Number, Date],
        disabled: { type: Boolean, default: false },
        autofocus: { type: Boolean, default: false },
        readonly: { type: Boolean, default: false },
        placeholder: {
            type: String,
            default() { return this.$t('selectDateText'); },
        },
        width: { type: [String, Number], default: 160 },
        alignment: {
            type: String,
            default: 'left',
            validator(value) {
                return ['left', 'right'].includes(value);
            },
        },
        time: {
            type: [String, Number],
            default: 'start',
        },
        yearDiff: {
            type: [String, Number],
            default: 3,
        },
        yearAdd: {
            type: [String, Number],
            default: 1,
        },
        reset: {
            type: Boolean,
            default: false,
        },
    },
    data() {
        return {
            showDate: this.format(this.date, 'YYYY-MM-DD'),
            lastDate: '',
        };
    },
    mixins: [MField],
    created() {
        if (this.minDate && this.maxDate) {
            const minDate = new Date(this.minDate);
            const maxDate = new Date(this.maxDate);
            if (minDate / MS_OF_DAY >> 0 > maxDate / MS_OF_DAY >> 0)
                throw new Calendar.DateRangeError(minDate, maxDate);
        }
        this.$emit('input', this.showDate ? new Date(this.showDate.replace(/-/g, '/')) : '');
        // document.addEventListener('click', this.fadeOut, false);
    },
    directives: { clickOutside },
    watch: {
        date(newValue) {
            this.showDate = this.format(newValue, 'YYYY-MM-DD');
        },
        showDate(newValue) {
            /**
             * @event change 
             * @property {object} sender 
             * @property {number} date  
             */
            const showDate = this.returnTime(newValue);
            const newDate = showDate ? new Date(showDate.replace(/-/g, '/')) : '';

            this.$emit('update:date', newDate);

            this.$emit('change', {
                sender: this,
                date: newDate,
            });

            this.$emit('input', newDate);
        },
        minDate(newValue) {
            return this.checkDate(newValue);
        },
        maxDate(newValue) {
            return this.checkDate(newValue);
        },
    },
    computed: {
        placement() {
            if (this.alignment === 'left')
                return 'bottom-start';
            else if (this.alignment === 'right')
                return 'bottom-end';
        },
    },
    methods: {
        checkDate(date) {
            if (!date)
                return;

            if (date === 'Invalid Date' || date === 'NaN')
                throw new TypeError('Invalid Date');
        },
        /**
         * @method select(date) 
         * @public
         * @param  {Date=null} date 
         * @return {void}
         */
        select(date) {
            if (this.readonly || this.disabled || this.isOutOfRange(date))
                return;

            this.showDate = this.format(date, 'YYYY-MM-DD');

            const showDate = this.returnTime(this.showDate);

            /**
             * @event select 
             * @property {object} sender 
             * @property {number} date  
             */
            this.$emit('select', {
                sender: this,
                date: new Date(showDate.replace(/-/g, '/')),
            });

            this.$refs.popper.toggle(false);
        },
        /**
         * @method onInput($event) 
         * @private
         * @param  {object} $event
         * @return {void}
         */
        onInput($event) {
            const value = $event.target.value;
            let date = value ? new Date(value.replace(/-/g, '/')) : null;
            this.lastDate = this.showDate;
            let showDate = '';
            if (date !== null && date.toString() !== 'Invalid Date') {
                date = this.isOutOfRange(date) || date;
                //     
                showDate = this.format(date, 'YYYY-MM-DD');
            } else
                showDate = this.$refs.input.value = this.format(this.lastDate, 'YYYY-MM-DD');
            this.showDate = showDate;
        },
        /**
         * @method isOutOfRange(date) 
         * @public
         * @param {Date} date 
         * @return {boolean|Date} date false
         */
        isOutOfRange(date) {
            let minDate = this.transformDate(this.minDate);
            let maxDate = this.transformDate(this.maxDate);

            // $watch`minDate``maxDate`
            minDate = minDate && minDate.setHours(0, 0, 0, 0);
            maxDate = maxDate && maxDate.setHours(0, 0, 0, 0);

            // minDate && date < minDate && minDate
            return (minDate && date < minDate && minDate) || (maxDate && date > maxDate && maxDate);
        },
        /**
         * @method toggle(flag) 
         * @public
         * @param {flag} true  false 
         */
        onToggle($event) {
            this.$emit('toggle', $event);
        },
        format,
        transformDate,
        closeCalendar() {
            if (this.showDate)
                this.$refs.calendar.updateShowDate(this.showDate);
        },
        returnTime(date) {
            if (!date)
                return;
            let time;
            if (this.time === 'start') {
                // 0:00:00
                time = '0:00:00';
            } else if (this.time === 'morning') {
                // 08:00:00
                time = '8:00:00';
            } else if (this.time === 'end') {
                // 23:59:59
                time = '23:59:59';
            } else if (typeof this.time === 'number') {
                // 
                if (this.time < 0)
                    throw new Error(this.$t('integerTip'));
                time = this.time < 24 ? this.time + ':00:00' : '23:59:59';
            } else {
                if (!/^[0-9]{1,2}:[0-9]{1,2}:[0-9]{1,2}$/.test(this.time))
                    throw new Error(this.$t('formatErrorTip'));
                time = this.time;
            }
            return date + ' ' + time;
        },
        resetValue() {
            this.showDate = undefined;
        },
    },
};

export default UDatePicker;

</script>
<style module src="./module.css"></style>
