<template src="./index.html"></template>
<script>
import { clickOutside } from '../../directives';
import { format, transformDate } from '../../utils/date';
import MField from '../m-field.vue';
import i18n from './i18n';
/**
 * @class DateTimePicker
 * @extend Dropdown
 * @param {object}                  options                     =  
 * @param {object=null}             options.date               <=> 
 * @param {string=''}         options.placeholder         => 
 * @param {Date|string=null}        options.minDate             => 
 * @param {Date|string=null}        options.maxDate             => 
 * @param {boolean=false}           options.autofocus           => 
 * @param {boolean=false}           options.readonly            => 
 * @param {boolean=false}           options.disabled            => 
 * @param {width|string|number}     options.width               => 
 */
export const UDateTimePicker = {
    name: 'u-date-time-picker',
    i18n,
    props: {
        disabled: { type: Boolean, default: false },
        placeholder: {
            type: String,
            default() { return this.$t('selectTimeText'); },
        },
        readonly: { type: Boolean, default: false },
        autofocus: { type: Boolean, default: false },
        minDate: [String, Number, Date],
        maxDate: [String, Number, Date],
        date: [String, Number, Date],
        width: {
            type: [String, Number],
            default: 170,
        },
        yearDiff: {
            type: [String, Number],
            default: 3,
        },
        yearAdd: {
            type: [String, Number],
            default: 1,
        },
    },
    mixins: [MField],
    data() {
        return {
            dateTime: this.format(this.date, 'YYYY-MM-DD HH:mm:ss'),
            open: false,
            minTime: undefined,
            maxTime: undefined,
            currentMaxDate: this.getMaxDate(), // 
        };
    },
    computed: {
        showTime() {
            return this.format(this.dateTime, 'HH:mm:ss');
        },
        showDate() {
            return this.format(this.dateTime, 'YYYY-MM-DD');
        },
        minCalendarDate() {
            return this.format(this.minDate, 'YYYY-MM-DD');
        },
        maxCalendarDate() {
            return this.format(this.currentMaxDate, 'YYYY-MM-DD');
        },
        spMinTime() {
            return this.format(this.minDate, 'HH:mm:ss');
        },
        spMaxTime() {
            return this.format(this.currentMaxDate, 'HH:mm:ss');
        },
        disabledNow() {
            const date = new Date();
            const { minDate, currentMaxDate } = this;
            let disabled = false;
            if (minDate) {
                disabled = date < this.transformDate(minDate);
            }
            if (!disabled && currentMaxDate) {
                disabled = date > this.transformDate(currentMaxDate);
            }
            return disabled;
        },
    },
    directives: {
        clickOutside,
    },
    created() {
        // vuewatchimmediatecreated u-field
        this.$emit('input', this.dateTime ? new Date(this.dateTime.replace(/-/g, '/')) : '');
    },
    watch: {
        date(newValue) {
            this.dateTime = this.format(newValue, 'YYYY-MM-DD HH:mm:ss');
        },
        dateTime(newValue) {
            // 

            if (newValue === 'Invalid Date' || newValue === 'NaN')
                throw new TypeError('Invalid Date');

            // 
            if (newValue) {
                const isOutOfRange = this.isOutOfRange(newValue);
                if (isOutOfRange)
                    newValue = this.format(isOutOfRange, 'YYYY-MM-DD HH:mm:ss');
            }

            this.$emit('update:date', newValue ? new Date(newValue.replace(/-/g, '/')).getTime() : '');

            /**
             * @event change 
             * @property {object} sender 
             * @property {object} date 
             */
            this.$emit('change', {
                sender: this,
                date: newValue ? new Date(newValue.replace(/-/g, '/')).getTime() : '',
            });

            // u-field
            this.$emit('input', newValue ? new Date(newValue.replace(/-/g, '/')) : '');
        },
        maxDate(value) {
            this.currentMaxDate = this.getMaxDate(value);
        },
        minDate(value) {
            this.currentMaxDate = this.getMaxDate();
        },
    },
    methods: {
        /**
         * @method outRangeDateTime(date, time) 
         * @private
         * @return {void}
         */
        outRangeDateTime(date, time) {
            if (!time)
                time = '00:00:00';

            if (date)
                date = new Date(date);
            else {
                this.$emit('select', {
                    sender: this,
                    date: '',
                });
                return;
            }
            time = time.split(':');
            date.setHours(time[0]);
            date.setMinutes(time[1]);
            date.setSeconds(time[2]);
            const datetime = this.format(date, 'YYYY-MM-DD');
            const dtime = this.format(date, 'HH:mm:ss');
            if (datetime === this.minCalendarDate && datetime === this.maxCalendarDate) {
                this.minTime = this.spMinTime;
                this.maxTime = this.spMaxTime;
            } else if (datetime === this.minCalendarDate)
                this.minTime = this.spMinTime;
            else if (datetime === this.maxCalendarDate)
                this.maxTime = this.spMaxTime;
            else if (datetime === this.minCalendarDate && dtime < this.spMinTime) {
                const spMinTime = this.spMinTime.split(':');
                date.setHours(spMinTime[0]);
                date.setMinutes(spMinTime[1]);
                date.setSeconds(spMinTime[2]);
            } else if (datetime === this.maxCalendarDate && dtime > this.spMaxTime) {
                const spMaxTime = this.spMaxTime.split(':');
                date.setHours(spMaxTime[0]);
                date.setMinutes(spMaxTime[1]);
                date.setSeconds(spMaxTime[2]);
            } else {
                this.minTime = undefined;
                this.maxTime = undefined;
            }
            // if (datetime === this.minCalendarDate || datetime === this.maxCalendarDate)
            this.dateTime = this.format(date, 'YYYY-MM-DD HH:mm:ss');

            this.$emit('select', {
                sender: this,
                date: new Date(date).getTime(),
            });
        },
        /**
         * @method onDateTimeChange(date, time) 
         * @private
         * @return {void}
         */
        onDateTimeChange(date, time) {
            if (!time)
                time = '00:00:00';

            date = new Date(date);
            time = time.split(':');
            date.setHours(time[0]);
            date.setMinutes(time[1]);
            date.setSeconds(time[2]);
            this.dateTime = this.format(date, 'YYYY-MM-DD HH:mm:ss');

            this.$emit('select', {
                sender: this,
                date: new Date(date).getTime(),
            });
        },
        /**
         * @method onInput($event) 
         * @private
         * @param  {object} $event
         * @return {void}
         */
        onInput($event) {
            const value = $event.target.value;
            this.updateDate(value);
        },
        updateDate(value) {
            let date = value ? new Date(value) : null;

            if (date !== null && date.toString() !== 'Invalid Date') {
                date = this.isOutOfRange(date) || date;
                this.dateTime = this.format(date, 'YYYY-MM-DD HH:mm:ss');
            } else {
                this.$refs.input.value = '';
                this.dateTime = '';
            }
        },
        setDateNow() {
            this.updateDate(new Date());
        },
        /**
         * @method isOutOfRange(date) 
         * @public
         * @param {Date} date 
         * @return {boolean|Date} date false
         */
        isOutOfRange(date) {
            date = this.transformDate(date);
            const minDate = this.transformDate(this.minDate);
            const maxDate = this.transformDate(this.currentMaxDate);

            // minDate && date < minDate && minDate
            return (minDate && date < minDate && minDate) || (maxDate && date > maxDate && maxDate);
        },
        toggle(value) {
            if (this.readonly)
                this.open = false;
            else
                this.open = value;
        },
        format,
        transformDate,
        handleClose() {
            this.open = false;
        },
        getMaxDate(value) {
            value = value || this.maxDate;
            const minTime = new Date(this.minDate).getTime();
            const maxTime = new Date(value).getTime();
            if (maxTime < minTime)
                return this.minDate;
            else
                return this.maxDate;
        },
    },
};

export default UDateTimePicker;

</script>
<style module src="./module.css"></style>
